@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  border-radius: 8px;
  font-family: 'Poppins',sans-serif;
  outline: none;
  
}
html,
body {
  height: 100%;
  width: 100%;
  background: #2f2f2f;
}
h1 {
  margin: 0;
}
.container {
  width:100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.todo-container {
  width:400px;
  height: auto;
  border: 1px solid black;
  padding: 3%;
  
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  
  background: #15D55B;
  color: #2f2f2f;
  
  box-shadow: 0 0 20px 0 #00000082;
}
.todos {
  width: 100%;
  
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  
  margin: 5% 0;
}
.todo {
  width: 100%;
  height: auto;
  
  background: #2f2f2f;
  color: white;
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  padding: 2% 3%;
  font-size: 14px;
  border:1px solid black;
  margin:5px;
}
.todo > button {
  background: transparent;
  border: none;
  color:white;
}

.add-todo {
  height:250px;
  width: 100%;
  
  background: #3E3E3E;
  color: white;
  
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  
  padding: 5%;
  margin-top: 5%;
}
.input-task {
  width: 100%;
  height: 40px;
  border: none;
  
  padding: 5px;
}
.btn {
  width: 100%;
  height: 45px;
  
  background: #2f2f2f;
  color: white;
  border: none;
  font-size: 14px;
}
.btn:hover {
  transform: scale(1.01);
  transition: all 0.3s ease;
}
label {
  width: 40%;
  font-size: 12px;
  
  display: flex;
  justify-content: space-around;
}
.completed {
  text-decoration: line-through;
  
}
